.posttext {
  white-space: pre-wrap;
}


body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

img {
  width: 100%;
}

.landing {
  position: relative;
  background: url('./img/showcase.jpg') no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin-top: -24px;
  margin-bottom: -50px;
}

.landing-inner {
  padding-top: 80px;
}

.dark-overlay {
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.card-form {
  opacity: 0.9;
}

.latest-profiles-img {
  width: 40px;
  height: 40px;
}

.form-control::placeholder {
  color: #bbb !important;
}




.ww {
  color: white !important;
}

.ww:hover {
  color: #343a40 !important;
}

.fa-twitter {
  color: #38a1f3;
}
.fa-facebook {
  color: #3b5998;
}
.fa-instagram {
  color: #3f729b;
}
.fa-youtube {
  color: #c4302b;
}
.fa-linkedin {
  color: #0077b5;
}
